@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");

@import "./blocks/card";
@import "./blocks/stars";

body {
  margin: 0;
  font-family: Roboto, Helvetica, sans-serif;
  font-weight: 400;
}

.product-code {
  font-size: 10px;
  line-height: 14px;
  color: #616070;
  margin-bottom: 16px;
}

.review {
  font-size: 10px;
  line-height: 14px;
  color: #060b35;
}

.card__price-label {
  font-size: 12px;
  line-height: 18px;
  color: #616070;
}

.card__price-money {
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: #060b35;
}
